  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
}

body {
  background-color: #fff;
  background-image: linear-gradient(to right bottom, #82c3f2, #94ccf5, #b9e1fd, #dff1ff, #f4faff);
  background-repeat: no-repeat;
  position: relative;
  padding: 0;
  margin: 0;
  line-height: 1.5;
  letter-spacing: 0.5px;
  color: #000;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}

main.main-container {
  margin-top: 86px;
}
@media (min-width: 767px) {
  main.main-container {
    margin-top: 0;
  }
}
main.main-container-fixed {
  margin-top: 86px;
}
@media (min-width: 767px) {
  main.main-container-fixed {
    margin-top: 96px;
  }
}

header.no-bg {
  background: #fff;
  height: auto;
  padding-bottom: 30px;
}
header.main, header.rest-page {
  background-position: center !important;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  height: 65vh;
  padding: 0 0 30px;
  position: relative;
  margin-top: 90px;
}
header.rest-page {
  height: 85vh;
}
header.no-height {
  height: auto !important;
  padding: 0 !important;
  margin-bottom: 100px !important;
  margin-top: 0 !important;
  background-size: auto !important;
}
header .justify-content-header {
  margin-top: 25vh;
}
header .justify-content-header.justify-content-header-fixed {
  margin-top: 25vh;
}
@media (min-width: 767px) {
  header .justify-content-header {
    margin-top: 25vh;
  }
}
header .justify-content-header h1.header {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 2.4rem;
  text-align: center;
  color: #fff;
}
header .justify-content-header h1.header span {
  color: #FBDE38;
}
@media (min-width: 767px) {
  header .justify-content-header h1.header {
    font-size: 6rem;
  }
}
header .justify-content-header h2.header {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 500;
  font-size: 2.4rem;
  text-align: center;
  color: #fff;
}
header .justify-content-header h2.header span {
  color: #FBDE38;
}
@media (min-width: 767px) {
  header .justify-content-header h2.header {
    font-size: 4rem;
    line-height: 3.7rem;
  }
}
@media (min-width: 990px) {
  header .justify-content-header h2.header {
    font-size: 4.4rem;
    line-height: 4.2rem;
  }
}
header .justify-content-header h1.header-error {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 700;
  font-size: 2.4rem;
  text-align: center;
  color: #000;
}
@media (min-width: 767px) {
  header .justify-content-header h1.header-error {
    font-size: 5.4rem;
  }
}
header .justify-content-header p {
  color: #000;
  font-family: "Roboto", sans-serif;
  font-size: 1.4rem;
  line-height: 2rem;
  font-weight: 400;
}
@media (min-width: 767px) {
  header .justify-content-header p {
    font-size: 2.4rem;
    line-height: 3.9rem;
  }
}
header .arrow-down {
  width: initial;
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  text-align: center;
}
header .arrow-down img {
  width: 65%;
