.login-page {
  background-image: url(/img/login-background.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom center;
}
.login-page .bg-gray {
  background-color: #f7f7f7;
}
.login-page .card-login {
  padding-top: 20vh;
  height: 100vh;
  overflow: hidden;
}
.login-page .card-login .card-header-login {
  text-align: center;
  font-size: 4rem;
  font-weight: 300;
  color: #000;
  margin: 30px 0;
}
.login-page .card-login .card-body-login form input.form-control {
  height: 60px;
  padding: 10px 20px;
  border: 1px solid #e6e6e6;
  border-radius: 10px;
  margin-bottom: 10px;
  font-size: 1.8rem;
  font-weight: 300;
  background-color: #f7f7f7;
